import ruiDatePicker from '@/components/rattenking-dtpicker/rattenking-dtpicker.vue'
import tTable from '@/components/t-table/t-table.vue'
import tTh from '@/components/t-table/t-th.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'

var d = getNowFormatDate();
function getNowFormatDate() {
	var array = new Array();
	var date = new Date();
	var seperator1 = "-";
	var seperator2 = ":";
	var month = date.getMonth() + 1;
	var strDate = date.getDate();
	if (month >= 1 && month <= 9) {
		month = "0" + month;
	}
	if (strDate >= 0 && strDate <= 9) {
		strDate = "0" + strDate;
	}
	var firstDayOfYear = date.getFullYear() + seperator1 + month + seperator1 + '01';
	var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate;      //当前时间  yyyy-MM-dd
	array.push(firstDayOfYear);
    array.push(currentdate);
	return array;
}

export default {
	components: {
		ruiDatePicker,
		tTable,
		tTh,
		tTr,
		tTd
	},
	data() {
		return {
			uid:'',
			tableList:[],
			value1:d[0],
			value2:d[1],
			huohao:'',
			worktype:'',
			color:'',
			chuanghao:'',
			chima:'',
			userid:'',  //编号
			sl1: '',
			sl2: '',
			zs: '',
			dk: '',
			
		}
	},
	onBackPress(options) {
		//console.log("###"+JSON.stringify(options));
		if (options.from === 'backbutton') {
			uni.switchTab({
				url: '/pages/index/index'
			});
			return true;
		}
		return false;
	},
	onLoad(r) {
		console.log("diyici:"+JSON.stringify(r)); 
		let temp = JSON.stringify(r);
		if(temp.indexOf('.html') < 0 && JSON.stringify(r) !='{}'){  
				this.huohao = r.huohao;
				this.chuanghao = r.chuanghao;
				this.color = r.color
				this.chima = r.chima
				this.userid = r.userid
				this.worktype = r.worktype
		}
		this.dk = this.websiteUrl
		uni.getStorage({
			key:'fwqsj',success: res => {
				this.dk = res.data
			}
		}),
		uni.getStorage({
			key:'loginData',success: res =>{				
				this.uid = res.data.uid
				uni.request({
					url:`https://${this.dk}/gongziHz.aspx`,
					method: 'GET',
					data: {
						dates:this.value1,
						datee:this.value2,
						huohao:this.huohao,
						chuanghao:this.chuanghao,
						color:this.color,
						chima:this.chima,
						userid:this.userid,
						uid:this.uid,
						worktype:this.worktype
					},
					success: res => {
						console.log(res);
						this.tableList = res.data
						let temp = res.data;
						let sum =0
						let sl1 =0;
						let sl2 = 0;
						for (let i = 0;i <temp.length;i++) {
							//console.log(temp[i].je);
							sum = sum+(temp[i].je?temp[i].je:0)
							sl1 = sl1+temp[i].sl
							sl2 = sl2 + temp[i].bs
						}
						this.zs = sum.toFixed(2);
						this.sl1 = sl1
						this.sl2 = sl2
					}
				})
			}			
		})
	},
	methods: {
		bindChange1(value){
			this.value1 = value;
		},
		bindChange2(value){
			this.value2 = value;
		},
		onpeDetailedMore(){
			//console.log(123);
			uni.navigateTo({
				url:'../info/info1?flag=4'
			})
		},
		onpeDetailed(){
			uni.request({
				url:`https://${this.dk}/gongziHz.aspx`,
				method: 'GET',
				data: {
					uid:this.uid,
					dates:this.value1,
					datee:this.value2,
					huohao:this.huohao,
					worktype:this.worktype	
				},
				success: res => {
					this.tableList = res.data
					let temp = res.data;
					let sum =0
					let sl1 =0;
					let sl2 = 0;
					for (let i = 0;i <temp.length;i++) {
						//console.log(temp[i].je);
						sum = sum+(temp[i].je?temp[i].je:0)
						sl1 = sl1+temp[i].sl
						sl2 = sl2 + temp[i].bs
					}
					this.zs = sum.toFixed(2);
					this.sl1 = sl1
					this.sl2 = sl2
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	
}
